html, body {
	width: 100vw;
	height: 100vh;
}

#root {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: fixed;
  overflow: hidden;
  width: 100vw;
  height: 100vh;
}

.App {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 360px;
  border: 1px solid;
}

.top-head {
  position: absolute;
  display: flex;
  justify-content: space-between;
  align-items: center;
  box-sizing: border-box;
  top: 0;
  width: 100%;
  height: 40px;
  padding: 5px;
  border-bottom: 1px solid;
  z-index: 2;
  opacity: 85%;
  background-color: white;
}

.hamburger {
  cursor: pointer;
  width: 56px;
  height: 35px;
  position: relative;
  top: 5px;
  left: 10px;
}

.hitit-text {
  color: red;
}

.line {
  background: black;
  margin-top: 6px;
  margin-bottom: 6px;
  width: 30px;
  height: 1px;
  border-radius: 2px;
  position: relative;
}

#map {
  width: 360px;
  height: 680px;
}

.panel {
  position: absolute;
  bottom: 0;
  z-index: 2;
  background-color: white;
  width: 100%;
  height: 94%;
  overflow: hidden;
  border-top: 1px solid;
  border-top-left-radius: 20px;
  border-top-right-radius: 20px;
  transition: 0.8s ease-in-out;
}

.top-arrow {
  display: flex;
  justify-content: center;
  width: 100%;
  height: 30px;
}

.arrow-up {
  margin-right: 20px;
  margin-top: 5px;
}

.arrow-up:after {
  content: " ";
  height: 0;
  width: 0;
  position: absolute;
  border: 15px solid transparent;
  border-top-width: 0;
  border-bottom-color: gray;
}

.panel-content {
  width: 100%;
  height: calc(100% - 30px);
  border-top: 1px solid;
  overflow-y: hidden;
  overflow-x: hidden;
}

.panel-top-wrap {
  display: inline-flex;
  gap: 5px;
  height: 161px;
}

.panel-list-view {
	overflow: scroll;
	max-height: 59vh;
}

.panel-image {
  pointer-events: auto;
}

.slider {
  position: relative;
  width: 335px;
  /* height: 130px; */
  overflow: hidden;
}

