.shot-add-box {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 20px;
  background-color: gray;
  height: 127px;
}

.panel-video {
  width: 125px;
  height: 125px;
}

.cafe-name {
  margin: 0;
  font-size: 12px;
}

.carousel-controler {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  cursor: pointer;
}

.sub-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: gray;
  padding: 10px;
}

.sub-p {
  color: white;
  margin: 0;
}
.sub-p:nth-child(4) {
  font-weight: 600;
}

.img-wrapper {
  padding: 10px 0px;
}

.img-wrapper > img {
  width: 340px;
  height: 220px;
}
